import { HttpErrorResponse, HttpStatusCode } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ThoughtForView } from '../../Dtos/thought-for-view';
import { ThoughtService } from '../../services/thought.service';

@Component({
  selector: 'app-thought-home',
  templateUrl: './thought-home.component.html'
})
export class ThoughtHomeComponent implements OnInit {
  thoughts: ThoughtForView[] = [];
  errorMessage: string = "";

  constructor(private thoughtService: ThoughtService) {}

  ngOnInit(): void {
      this.thoughtService.getRecentThoughts().subscribe({
        next: res => {
        this.thoughts = res;
      },
      error: (error: HttpErrorResponse) => {
        console.error(error);
        if (error.status === HttpStatusCode.InternalServerError) {
          this.errorMessage = "There was an error. The system must be having a bad day. Try again later."
        } else if (error.status === HttpStatusCode.Unauthorized) {
          this.errorMessage = "Could not get data. You were unauthorized. Please sign in."
        }
      }
    });
  }

}
